export const logout = async ( ) => {
  try {
    const response = await instance.post(`${REACT_BACKEND_URL}/v1/user/logout`);
    return response;
  } catch ( error ){
    return error.response;
  }
};
interface PasswordBody{
  oldPassword:string,
  newPassword:string
}
export const changePassword = async (passwordBody:PasswordBody) => {
  try{
    const response = await instance.post(`${REACT_BACKEND_URL}/v1/user/changepassword`,passwordBody);
    return {
      status:true,
      message:response.data.message
    };
  }catch(error){
    return {
      status:false,
      message:error.response.data.message

    };
  }
};